class InSight {
    constructor(els, selector, func){
        this.els = els
        if(!this.els.length) return false
        this.func = func
        this._init()
    }
    _init(){
        const options = {
            rootMargin: `0px 0px -30% 0px`
        }
        const io = new IntersectionObserver(entries => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    if(this.func) this.func(entry.target)
                }
            })
        }, options);

        this.els.forEach(el => {
            io.observe(el);
        })
    }
}
class InSightFactory {
	constructor(props) {
		this.elements = [...document.querySelectorAll(`[${props.selector}]`)];
		
		new InSight(this.elements, props.selector, props.func)
		
	}
}

export {InSight, InSightFactory}